header{
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
header .logo{
    padding:0 20px;
}
header .logo h1{
    color: #333;
}
header ul{
    display: flex;
}
header ul li{
    padding:0 20px;
    list-style: none;
}
header ul li a{
    cursor: pointer;
    padding: 5px 10px;
    transition: 0.5s;
    border-radius: 20px;
}
header ul li a:hover{
    background-color: #333;
    color: #fff;
}
@media screen and(max-width: 900px) {header .logo h1{
        font-size: 20px;   
    }
    header ul li{
        padding: 0 10px;
    }
    header ul li a {
        font-size: 10px;
    }
}
