.about {
    width: 100%;
    height: 800px;
}
.about-img {
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.about-img img {
    width: 500px;
    height: 500px;
}
@media screen and (max-width: 900px) {
    .about {
        width: 100%;
        height: 500px;
    }
    .about-img {
        width: 100%;
        height: 50%;
    }
    .about-img img {
        width: 200px;
        height: 200px;
    }
    .about-text {
        text-align: center;
    }
}
