.home {
    width: 100%;
    height: 600px;
    background: url('http://service.rsu.ac.th/Helpfund/images/new1.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-attachment: fixed;
}
.container-flex{
    display: flex;
    height: 100%;
    width: 100%;
    background: rgba(255, 255, 255, 0.4);
}
.container-flex .text-flex{
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}
.container-flex .text-flex .home-text h1{
    text-align: center;
    font-size: 60px;
}
.container-flex .text-flex .home-text h1 span{
    color: navy;
}
.container-flex .text-flex .home-textp{
    text-align: center;
    font-size: 20px;
}
.container-flex .home-img{
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 50%;
}
.home-img img{
    width: 400px;
    height: 400px;
}
@media screen and(max-width: 900px) {
    .home{
        height: auto;
    }
    .container-flex{
        flex-direction: column;
    }
    .container-flex .text-flex .home-text h1 {
        font-size: 40px;
    }
    .container-flex .text-flex,.container-flex .home-img {
        width: 100%;
    }
}
        